import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { HomeComponent } from './home/home.component';
import { CustomerComponent } from './home/customer/customer.component';
import { VendorComponent} from './home/vendor/vendor.component';
import { AuthGuard } from './shared/auth.guard';
import { ManageCustomerComponent } from './home/manage-customer/manage-customer.component';

const routes: Routes = [
  { path: 'login', component: LoginComponent },
  {
    path: '',
    component: HomeComponent,
    runGuardsAndResolvers: 'always',
    canActivate: [AuthGuard],
    children: [
      { path: 'customer', component: ManageCustomerComponent },
      { path: 'customerdetail', component: CustomerComponent  },
      { path: 'customerdetail/:id', component: CustomerComponent },
      { path: 'vendor', component: VendorComponent }
    ]
  },

  { path: '', redirectTo: '/customer', pathMatch: 'full' },
  { path: '**', redirectTo: '/customer', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
